import React from 'react';
import "./style.css"
import { fetchToDoList, updateToDoList, isLogin, getRequestToDoList } from './backend.js';
import { SideNav, Navbar } from "./reused-components/navigation.js";
import { Redirect } from "react-router-dom";
import { ToDoDraggable } from "./ToDo.js";
import "./reused-components/navigation.css";
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';





export class WorkspacePage extends React.Component {
  constructor(props) {
    super(props)
    getRequestToDoList()
    this.state = { isExtended: "extended", toDoList: fetchToDoList(), }
    window.addEventListener("resize", this.updateSize)
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended", toDoList: this.state.toDoList }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended", toDoList: this.state.toDoList, }
      })
    }
  }

  updateToDo = (list, index) => {
    var temp_list = this.state.toDoList;
    temp_list[index] = list;
    updateToDoList(temp_list);
    this.setState((props, state) => {
      return { isExtended: this.state.isExtended, toDoList: temp_list, }
    })
  }


  addToToDo = (item, index) => {
    if (index === this.state.toDoList.length) {
      return;
    }
    var temp_list = this.state.toDoList;
    temp_list[index].push(item);
    updateToDoList(temp_list);
    this.setState((props, state) => {
      return { isExtended: this.state.isExtended, toDoList: temp_list, }
    })
  }
  render() {
    getRequestToDoList()
    return (
      <div>
        <div style={{ position: "absolute" }}><Navbar pageName="Your Workspace" sideNavAnimation={this.changeState} /></div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}><SideNav isExtended={"extended"} /></div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : {width: "100vw"})}>
            <div style={{ width: "1200px" }}>
              <ToDoDraggable />
            </div>
            <div style={{position: "fixed", bottom: "15px", marginLeft: "50px", }}>
            <SprintDates />
            </div>
          </div>
 
          <div>
          

          </div>
        </div>


      </div>
    )
  }
}



class SprintDates extends React.Component {
  constructor(props) {
    super(props);
    this.state= {popUp: false, firstSprint: "m/d - m/d", numSprints: 0}
  }
  changeSoonestSprint = (sprint, num) => {
    var stringSprint = sprint[0] + " - " + sprint[1]
    this.setState({firstSprint: stringSprint, numSprints: num})
  }
  changePopUp = () => this.setState({popUp: !this.state.popUp})
  render() {
    return (
      <>
      {this.state.popUp ? <SprintDatesPopUp changePopUp={this.changePopUp} changeSoonestSprint={this.changeSoonestSprint}/> : null}
      <div style={{cursor: "pointer"}} onClick={this.changePopUp}>
        
      <svg xmlns="http://www.w3.org/2000/svg" width="400" height="auto" viewBox="0 0 683 84">
  <rect id="Rectangle_1467" data-name="Rectangle 1467" width="683" height="84" rx="30" fill="#f8f8f8"/>
  <rect id="Rectangle_1495" data-name="Rectangle 1495" width="62" height="55" rx="18" transform="translate(151 15)" fill="#fff"/>
  <text id="Sprint_" data-name="Sprint#" transform="translate(37 56)" stroke="#707070" stroke-width="1" font-size="30" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0">Sprint#</tspan></text>
  <rect id="Rectangle_1496" data-name="Rectangle 1496" width="185" height="55" rx="18" transform="translate(448 15)" fill="#fff"/>
  <text id="Dates_" data-name="Dates    " transform="translate(355 54)" stroke="#707070" stroke-width="1" font-size="30" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0" xmlSpace="preserve">Dates    </tspan></text>
  <text id="m_d_-_m_d_" data-name="m/d - m/d    " transform="translate(465 51)" stroke="#707070" stroke-width="1" font-size="27" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0" xmlSpace="preserve">{this.state.firstSprint}</tspan></text>
  <text id="_1" data-name="1" transform="translate(173 54)" stroke="#707070" stroke-width="1" font-size="27" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0">{this.state.numSprints}</tspan></text>
</svg>
</div>
</>
    )
  }
}

class SprintDatesPopUp extends React.Component {
  constructor(props) {
    super(props)
    this.state = {dates: ["m/d", "m/d"], error: [null, null], allDates: []}
  }
  inputChangeDates = (date, index) => {
    try {
      var tempDateList = date.split("/")
      parseInt(tempDateList[0])
      parseInt(tempDateList[1])
      var tempDate = this.state.dates;
    tempDate[index] = date
    }
    catch {
      var tempDate = this.state.dates
      return;
    }
    
    this.setState({dates: tempDate})
  }
  addAllDates = () => {
    var tempAllDates = this.state.allDates;
    tempAllDates.push(this.state.dates)
    this.props.changeSoonestSprint(soonestSprint(this.state.allDates), this.state.allDates.length)
    this.setState({dates: ["m/d", "m/d"]})
  }
  render() {
    return (<>
    <div style={{position: "fixed", bottom: "0", left: "0", height: "100vh", width: "100vw", justifyContent: "center", alignItems: "center", display: "flex"}} >
    <div style={{height: "60vh", width:" 45vw", backgroundColor: "white", zIndex: "20", position: "relative", padding: "15px", borderRadius: "20px"}}>
      <Calendar selectRange={true} onChange={(value) => this.setState({dates: makeDisplayDate(value)})} value={makeDateObject(this.state.dates)} className="react-calendar"/>
      <div style={{position: "absolute", right: "30px", top: "30px"}}>
        <div>Start: <input style={{width: "100px"}} placeholder={this.state.dates[0]} onChange={(text) => this.inputChangeDates(text.target.value, 0)}/>{this.state.error[0]}</div>
        <div>End: <input  style={{width: "100px"}} placeholder={this.state.dates[1]} onChange={(text) => this.inputChangeDates(text.target.value, 1)}/>{this.state.error[1]}</div>
        <button onClick={this.addAllDates}>Add Sprint</button>
        <div style={{display: "block"}}>
          
          {this.state.allDates.map(content => <div>{"start: " + content[0] + " end: " + content[1]}</div>)}
        </div>
        </div>
    </div>
    <div style={{position: "fixed", bottom: "0", left: "0", height: "100vh", width: "100vw", backgroundColor: "black", opacity: "50%"}}
    onClick={this.props.changePopUp}>
    </div>
    
    </div>
    </>)
  }
}
function makeDateObject(dateList) {
  if ((dateList[0] === "m/d") || (dateList[1]  === "m/d")) {
    return null
  }
  return dateList.map(date => {
    var tempDateList = date.split("/")
    return new Date(parseInt(
      parseInt(JSON.stringify(new Date()).substr(1,4))
    ),parseInt(tempDateList[0])-1, parseInt(tempDateList[1]))
  })
}

function makeDisplayDate(dateList) {
  return dateList.map(date => {
    return `${date.getMonth()+1}/${date.getDate()}`
  })
}

function soonestSprint(datesList) {
  if (datesList.length === 0) {
    return ["m/d", "m/d"]
  }
  
  var dateIndex = 0;
  var dates = 1000000000
  datesList.forEach((date, index) => {
    var dateSplit = date[1].split("/")
    var integerDate = parseInt(dateSplit[0])*30 + parseInt(dateSplit[1])
    if (integerDate < dates) {
      dateIndex = index
      dates = integerDate
    }
  })
  return datesList[dateIndex]
}


